#expenses {
	.advanced-controls {
		.row-fluid {
			margin-top: 10px;
		}
		.total-range input {
			max-width: 100px;
		}
		input[type="date"] {
			max-width: 140px;
		}
	}

	#expense-table {
		tr.expense-summary div.col:first-child:before {
	    content: "\f078"; // fontawesome fa-chevron-down
	  	margin-right: 4px; 
	  	font-family: "FontAwesome";
	    font-style: normal;
	    font-weight: normal;
	    font-size: 70%;
	    margin-left: -20px;
	    cursor: pointer;
		}
		tr.expense-summary.closed div.col:first-child:before {
	  	content: "\f054"; // fontawesome fa-chevron-right
	  	margin-right: 4px; 
	  	font-family: "FontAwesome";
	    font-style: normal;
	    font-weight: normal;
	    font-size: 70%;
	    margin-left: -20px;
	    cursor: pointer;
		}

		th span {
			font-size: 115%;
			text-decoration: underline;
		}
	}

	#expense-table.open {
		tr.expense-summary div.col:first-child:before {
	  	content: "\f078"; // fontawesome fa-chevron-down
	  	margin-right: 4px; 
	  	font-family: "FontAwesome";
	    font-style: normal;
	    font-weight: normal;
	    font-size: 70%;
	    margin-left: -20px;
	    cursor: pointer;
		}
	}

	tr.expense-items {
		.headers, .expense-item {
			padding: 5px 0;
		}
		.headers div {
		  text-decoration: underline;
		}
		.expense-item {
			border-top: 1px solid #EEEEEE;
		}
	}

}