#project-overview,
#project-category {
	.tab-pane {
		overflow-x: scroll;
	}
	
	table {
		width: 100%;

		thead th {
			text-align: right;
		}

		thead th:first-child {
			text-align: left;
		}

		tbody {
			td, 
			th {
				text-align: right;
				padding: 2px 4px;
				font-size: 15px;
				line-height: 20px;
			}
			th {
				border-top: 1px solid #DDDDDD;
			}
			td:first-child,
			th:first-child {
				text-align: left;
				border-top-width: 0;
			}

			.expense-item-count {
				font-size: 10px;
				margin-right: 6px;
			}

			tr.expense-items {
				display: none;
				background: #f9f9f9;

				td {
					padding: 6px;
				}
				table {
					td, th {
						font-size: 12px;
						background: #ffffff;
						text-align: left;
					}
					td:first-child,
					th:first-child {
						border-top-width: 1px;
					}
					td.amount {
						text-align: right;
					}
				}
			}
		}
	}

	i.fa-info-circle.category-description {
	  font-size: 11px;
	  vertical-align: top;
	  cursor: pointer;
	  margin-left: 2px;
	  color: #636e7b;
	}
}
#projects-archived {
	.archived-project {
		a:hover {
			text-decoration: none;
		}
	}
}